*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100vh;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  background-color: #1B2931;
  background-image: url("../img/background-header.png");
  background-repeat: no-repeat;
  background-position: top center;
  overflow-x: hidden; }

footer {
  margin-top: auto; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* *****************************
*** 2. Вспомогательные стили ***
***************************** */
.container {
  width: 100%;
  max-width: 1424px;
  margin: 0 auto;
  padding: 0;
  outline: 2px solid red; }

.button {
  display: inline-block;
  width: auto;
  min-width: 180px;
  min-height: 45px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 700;
  line-height: 16px;
  color: #FFFFFF;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #FFFFFF;
  border-radius: 5px; }

/*
==================================
--------- МЕДИАВЫРАЖЕНИЯ ---------
==================================
*/
@media (max-width: 767px) {
  .logo img {
    width: 38px;
    height: 38px;
    object-fit: cover;
    object-position: 0 0; } }

/* **********************
*** 3. Основные стили ***
********************** */
.site-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 33px;
  padding-left: 13px;
  padding-right: 13px; }

.site-header__contacts ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

.site-header__contacts li {
  margin-right: 113px; }
  .site-header__contacts li:last-child {
    margin-right: 0; }

.site-header__contacts-link {
  position: relative;
  color: #FFFFFF;
  text-decoration: none;
  transition-property: color, opacity;
  transition-duration: 0.15s;
  transition-timing-function: ease; }

.site-header__contacts-link-icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  fill: #00B2FF;
  transition-property: fill;
  transition-duration: 0.15s;
  transition-timing-function: ease; }
  .site-header__contacts-link-icon--mail {
    left: -38px; }
  .site-header__contacts-link-icon--phone {
    left: -34px; }

.site-header__callback-button {
  margin-left: 39px;
  cursor: pointer;
  transition-property: background-color, border-color, opacity;
  transition-duration: 0.15s;
  transition-timing-function: ease; }

/*
*** Стили для интерактивности ***
*/
.site-header__contacts-link:hover,
.site-header__contacts-link:focus {
  color: #00B2FF; }

.site-header__contacts-link:hover .site-header__contacts-link-icon,
.site-header__contacts-link:focus .site-header__contacts-link-icon {
  fill: #FFFFFF; }

.site-header__contacts-link:active {
  opacity: 0.7; }

.site-header__callback-button:hover,
.site-header__callback-button:focus {
  background-color: #00B2FF;
  border-color: #00B2FF; }

.site-header__callback-button:active {
  opacity: 0.7; }

/*
==================================
--------- МЕДИАВЫРАЖЕНИЯ ---------
==================================
*/
@media (max-width: 1023px) {
  .site-header {
    margin-top: 20px;
    padding-right: 25px;
    padding-left: 25px; }
  .site-header__logo {
    margin: 0; }
  .site-header__contacts li {
    margin-right: 34px;
    padding-top: 7px; }
  .site-header__contacts-link {
    font-size: 0; }
  .site-header__contacts-link-icon {
    position: static;
    transform: none; }
    .site-header__contacts-link-icon--mail {
      width: 26px;
      height: 19px; }
    .site-header__contacts-link-icon--phone {
      width: 24px;
      height: 24px; }
  .site-header__callback-button {
    display: none; } }

@media (max-width: 767px) {
  .site-header {
    margin-top: 15px;
    padding-right: 17px;
    padding-left: 17px; }
  .site-header__contacts li {
    margin-right: 21px; } }

.site-footer {
  width: 100%;
  min-height: 317px;
  background-color: #FFFFFF; }

.site-footer__info-section {
  border-bottom: 1px solid rgba(98, 105, 98, 0.2); }

/* **********************
*** 4. Модальные окна ***
********************** */
.modal {
  display: none; }
  .modal--show {
    display: flex; }
  .modal--overlay {
    position: fixed;
    z-index: 1;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5); }
  .modal--window {
    flex-direction: column;
    width: 100%;
    max-width: 490px;
    min-height: 605px;
    margin: auto;
    background-color: #00B2FF; }

.modal p {
  margin: 0 auto;
  padding: 0 20px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 140.62%;
  color: #FFFFFF; }
